import { Component, OnInit } from '@angular/core';
import { environment } from '../../../environments/environment';
import { HttpClient } from '@angular/common/http';
import { AppService, AuthService, StoreService } from '@monorepo/utils';
import { HfButtonComponent, HfCardComponent, HfPriceComponent } from '@monorepo/ui';
import { URLS } from '../../shared/utils/urls';
import { Router } from '@angular/router';
import { CommonModule } from '@angular/common';
import { OrderDetailsBarComponent } from '../../shared/components/order-details-bar/order-details-bar.component';

@Component({
  selector: 'app-fuel-additive',
  standalone: true,
  imports: [CommonModule, HfCardComponent, HfButtonComponent, OrderDetailsBarComponent, HfPriceComponent],
  templateUrl: './fuel-additive.component.html',
  styleUrl: './fuel-additive.component.scss'
})

export class FuelAdditiveComponent implements OnInit {
  getProviderOilApiData: object;
  regularPerGallonPrice: number;
  regularTotalPrice: number;
  fuelAdditivePerGallonPrice: number;
  fuelAdditiveTotalPrice: number;
  orderParams: object;
  benefits: any[] = [
    "Boost efficiency",
    "Cut heating costs",
    "Reduce soot by 50%",
    "Dissolve sludge",
    "Clean filters & nozzles",
    "Prevent jelling",
    "Prohibit bacteria",
    "Eliminate water"
  ]

  constructor(
    private http: HttpClient,
    public storeService: StoreService,
    private router: Router,
    private authService: AuthService,
    public appService: AppService,
  ) { }

  ngOnInit() {
    if (this.appService.isBrowserRendering) {
      this.storeService.getValueFromStore('orderParams', (orderParams) => {
        this.orderParams = orderParams;
        this.callGetProviderOilApi();
      });
    }
  }

  callGetProviderOilApi() {
    const getProviderOilApiUrl = `${environment.api_url}customer/offer/get-provider-oil`;
    const body = {
      "zip": this.orderParams['zipCode'],
      "amount": this.orderParams['quantity'],
      "orderType": Number(this.orderParams['orderType']),
      "payWithCash": this.orderParams['mode'] == 1 ? true : false,
      "day": this.orderParams['dayId'],
      "companyId": this.orderParams['companyId'],
      "isFlashSale": this.orderParams['isFlashSale'] == null ? false : true,
      "heatFleetFlashSaleId": this.orderParams['heatFleetFlashSaleId']
    };
    this.http.post(getProviderOilApiUrl, body).subscribe({
      next: (res) => {
        this.getProviderOilApiData = res;
        this.regularPerGallonPrice = this.getProviderOilApiData['provider']['price'];
        this.regularTotalPrice = this.getProviderOilApiData['provider']['calculatedPrice'];

        this.fuelAdditivePerGallonPrice = (
          this.getProviderOilApiData['provider']['premiumFuelAdditivePrice'] +
          this.getProviderOilApiData['provider']['price']
        );

        this.fuelAdditiveTotalPrice = Number((this.fuelAdditivePerGallonPrice * Number(this.orderParams['quantity'])));

        if (this.getProviderOilApiData['provider']['isFlatFee']) {
          this.fuelAdditiveTotalPrice += this.getProviderOilApiData['provider']['flatFee'];
        }
      },
      error: (err) => {
        console.log(err);
      },
    });
  }

  isAdditive(isAdditive: boolean) {
    this.storeService.getValueFromStore('orderParams', (orderParams) => {
      let orderParamsUpdate = { ...orderParams };
      orderParamsUpdate['fuel-additive'] = isAdditive;
      if (isAdditive) {
        orderParamsUpdate['price'] = this.fuelAdditivePerGallonPrice;
        orderParamsUpdate['calculatedPrice'] = this.fuelAdditiveTotalPrice;
      } else {
        orderParamsUpdate['price'] = this.regularPerGallonPrice;
        orderParamsUpdate['calculatedPrice'] = this.regularTotalPrice;
      }
      this.storeService.addValueToStore('orderParams', orderParamsUpdate);
      if (this.authService.isUserLoggedIn) {
        this.router.navigate([URLS.CONFIRM_ADDRESS])
      } else {
        this.router.navigate([URLS.ORDER_LOGIN])
      }
    });
  }
}
